@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply bg-primary-darkest; /* bg-gray-700 */
}

@font-face {
    font-family: smooth;
    src: url(assets/Nunito-Regular.ttf);
}

body {
    font-family: smooth;
}

.cluster-status-up {
    @apply font-normal text-green-600;
}

.cluster-status-down {
    @apply font-normal text-red-700;
}

.cluster-page-text span {
    @apply font-bold;
}

.cluster-status-stat p {
    @apply inline-block;
}

.cluster-status-stat span {
    color: #fff;
}

.cluster-stat-progress {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(1px);
}

.icon-rotation-animate {
    animation-name: spin;
    animation-duration: 5000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.progress-bar div {
    transition: all 2s;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
